import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import ShowMovie from "../components/ShowMovie";
import Loading from "../components/Loading";

function Detail() {
  const [loading, setLoading] = useState(true);
  const [mvi, setMvi] = useState({});
  const { id } = useParams();

  const getMovie = async () => {
    const json = await (
      await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)
    ).json();
    setLoading(false);
    setMvi(json.data.movie);
  };
  useEffect(() => {
    getMovie();
  }, []);
  return (
    <>
      {loading ? (
        <Loading />
      ) : (
        <ShowMovie
          bgi_o={mvi.background_image_original}
          descript={mvi.description_intro}
          download_c={mvi.download_count}
          genres={mvi.genres}
          cover_img={mvi.large_cover_image}
          like={mvi.like_count}
          runtime={mvi.runtime}
          title={mvi.title}
          year={mvi.year}
          url={mvi.url}
        />
      )}
    </>
  );
}

export default Detail;
