import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import styled from "styled-components";
import errorImg from "../noImg.png";

function Movie({ id, coverImg, title, year, summary, genres }) {
  const onErrorImg = (e) => (e.target.src = { errorImg });
  return (
    <MovieWrapper>
      <MainImg src={coverImg} alt={title} title={title} onError={onErrorImg} />
      <LinkMovieDetail to={`/movie/${id}`}>
        {title} ({year})
      </LinkMovieDetail>
      <BoldText>Summary</BoldText>
      <p>{summary ? `${summary.slice(0, 135)}...` : "-"}</p>
      <BoldText>Genres</BoldText>
      {genres !== "" && genres !== undefined ? (
        <GenresBox>
          {genres.map((g) => (
            <GenresEle key={g}>{g}</GenresEle>
          ))}
        </GenresBox>
      ) : (
        "-"
      )}
    </MovieWrapper>
  );
}
const MovieWrapper = styled.section`
  width: 400px;
  text-align: center;
  padding: 15px 10px;
  background-color: aliceblue;
  border-radius: 20px;
  &:hover img {
    opacity: 1;
  }
`;
const MainImg = styled.img`
  opacity: 0.7;
  transform: translateY(-30px);
  display: block;
  margin: auto;
`;
const LinkMovieDetail = styled(Link)`
  display: block;
  height: 80px;
  font-size: 24px;
  font-weight: 700;
`;
const BoldText = styled.p`
  font-size: 20px;
  font-weight: 700;
  height: 30px;
`;
const GenresBox = styled.div`
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
`;
const GenresEle = styled.p`
  &:nth-of-type(1):before {
    display: none;
  }
  &::before {
    content: "·";
    margin-right: 10px;
  }
`;

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  coverImg: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  year: PropTypes.number.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string),
};

export default Movie;
