import { useEffect, useState } from "react";
import Movie from "../components/Movie";
import Loading from "../components/Loading";
import styled from "styled-components";
import axios from "axios";

function Home() {
  const [loading, setLoading] = useState(true);
  const [movies, setMovies] = useState([]);
  const [input, setInput] = useState("");

  const getMovies = async () => {
    const json = await (
      await fetch(
        "https://yts.mx/api/v2/list_movies.json?minimum_rating=7.5&sort_by=year"
      )
    ).json();
    setMovies(json.data.movies);
    setLoading(false);
  };
  useEffect(() => {
    getMovies();
  }, []);

  const onChange = (e) => setInput((prev) => e.target.value);
  const onClick = (e) => {
    e.preventDefault();
    searchMovies(input);
  };

  const searchMovies = async () => {
    const {
      data: {
        data: { movies: searchMovie },
      },
    } = await axios.get(
      `https://yts.mx/api/v2/list_movies.json?query_term=${input}`
    );
    setMovies(searchMovie);
  };
  return (
    <>
      {loading ? (
        <Loading />
      ) : (
        <>
          <SearchBox>
            <SearchInput onChange={onChange} value={input}></SearchInput>
            <button onClick={onClick}>검색</button>
          </SearchBox>
          <Container>
            {movies !== undefined ? (
              movies.map((movie) => (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  coverImg={movie.medium_cover_image}
                  title={movie.title}
                  year={movie.year}
                  summary={movie.summary}
                  genres={movie.genres}
                />
              ))
            ) : (
              <p>찾으시는 영화가 없습니다..😫</p>
            )}
          </Container>
        </>
      )}
    </>
  );
}

const Container = styled.section`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px 20px;
  margin-top: 20px;
`;
const SearchBox = styled.div`
  display: block;
  margin: 0 0 40px 5%;
`;
const SearchInput = styled.input.attrs({
  type: "text",
  placeholder: "search movie",
})`
  margin-right: 20px;
`;

export default Home;
